//import useState hook to create menu collapse state
import React, { useState } from "react";
import mypic from '../img/mypic.JPG'; 
//import react pro sidebar components
import {
  ProSidebar,
  Menu,
  MenuItem,
  SidebarHeader,
  SidebarFooter,
  SidebarContent,
} from "react-pro-sidebar";

//import icons from react icons
import { FaList, FaRegHeart } from "react-icons/fa";
import {
  FiHome,
  FiLogOut,
  FiArrowLeftCircle,
  FiArrowRightCircle,
} from "react-icons/fi";
import { RiPencilLine } from "react-icons/ri";
import { BiCog } from "react-icons/bi";

//import sidebar css from react-pro-sidebar module and our custom css
import "react-pro-sidebar/dist/css/styles.css";
import "./Header.css";

const Header = () => {
  //create initial menuCollapse state using useState hook
  const [menuCollapse, setMenuCollapse] = useState(false);

  //create a custom function that will change menucollapse state from false to true and true to false
  const menuIconClick = () => {
    //condition checking to change state from true to false and vice versa
    menuCollapse ? setMenuCollapse(false) : setMenuCollapse(true);
  };

  return (
    <>
      <div id="header">
        {/* collapsed props to change menu size using menucollapse state */}
        <ProSidebar collapsed={menuCollapse}>
          <SidebarHeader>
            <div className="logotext">
              {/* small and big change using menucollapse state */}
              <p>{menuCollapse ? "Aman" : "Aman Resume"}</p>
            </div>
            <div className="closemenu" onClick={menuIconClick}>
              {/* changing menu collapse icon on click */}
              {menuCollapse ? <FiArrowRightCircle /> : <FiArrowLeftCircle />}
            </div>
          </SidebarHeader>
          <SidebarContent>
            <Menu iconShape="square">
              <MenuItem active={true} icon={<FiHome />} onClick="s">
                Home
              </MenuItem>
              <MenuItem icon={<FaList />} onClick="#personal">
                Personal Summary
              </MenuItem>
              <MenuItem icon={<FaRegHeart />} onClick="#skill">
                Skill Set
              </MenuItem>
              <MenuItem icon={<RiPencilLine />} onClick="#exp">
                Experiance
              </MenuItem>
              <MenuItem icon={<BiCog />}>Projects</MenuItem>
            </Menu>
          </SidebarContent>
          <SidebarFooter>
            <Menu iconShape="square">
              <MenuItem icon={<FiLogOut />}>Logout</MenuItem>
            </Menu>
          </SidebarFooter>
        </ProSidebar>

        {menuCollapse ? (
          <div className="home1" id="home">
            <h1>Home</h1>
            <img src={mypic} alt="Logo" className="mypicimg"/>
            <h2 style={{color: "gray",padding:50,lineHeight:2,fontFamily:'cursive' ,textAlign:"center",justifyContent:"center"}}>
              To work in a challenging environment which will help me to explore
              my skills in an effective way, working towards achieving the goals
              of the organization with career growth, to become a leader in my
              area of work and definitely work for the betterment of the society
              as currently too doing.
            </h2>
          </div>
        ) : (
          <div className="home2" id="home">
            <h1>Home</h1>
            <img src={mypic} alt="Logo" className="mypicimg"/>
            <h2 style={{color: "gray",padding:50,lineHeight:2,fontFamily:'cursive' ,textAlign:"center",justifyContent:"center"}}>
              To work in a challenging environment which will help me to explore
              my skills in an effective way, working towards achieving the goals
              of the organization with career growth, to become a leader in my
              area of work and definitely work for the betterment of the society
              as currently too doing.
            </h2>
          </div>
        )}

        {/* <div className="personal" id="personal">
          <h1>personal</h1>
        </div>
        <div className="skill" id="skill">
          <h1 >skiil</h1>
        </div>
     
      <div className="exp" id="exp">
        <h1>experiance</h1>
      </div>
      <div className="projects" id="projects">
        <h1>Projects</h1>
      </div> */}
      </div>
    </>
  );
};

export default Header;
